<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <core-progress-modal :show="loading" />
      <v-layout
        v-if="!loading"
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Defina os parâmetros do cálculo</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md3>
                  <core-label-value
                    :label="'Rede'"
                    :value="value.redeNome"
                  />
                </v-flex>
                <v-flex md3>
                  <core-label-value
                    :label="'Empresa'"
                    :value="value.empresaNome"
                  />
                </v-flex>
                <v-flex md6>
                  <core-label-value
                    :label="'Loja'"
                    :value="value.lojaNome"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.vencimentoAte"
                    label="Vencimento até"
                    type="date"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.pagamentoEm"
                    label="Pagamento em"
                    type="date"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.peComissaoCompra"
                    v-model.lazy="value.peComissaoCompra"
                    type="tel"
                    prefix="%"
                    maxlength="5"
                    label="Comissão compras (% a.m.)"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.peDescontoAntecipacao"
                    v-model.lazy="value.peDescontoAntecipacao"
                    type="tel"
                    prefix="%"
                    maxlength="5"
                    label="Desconto antecipação (% a.m.)"
                  />
                </v-flex>
                <v-flex md4>
                  <v-checkbox
                    v-model="value.empurrarSaldoCredor"
                    label="Empurrar saldo credor para o próximo repasse"
                    class="body-2"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              dark
              @click="goBack">Voltar</v-btn>

            <v-btn
              :disabled="disableBtn"
              type="submit"
              color="primary">Continuar</v-btn>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import { MONEY } from '../../../constants'
import { LojaBusiness } from '../../../business'
export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    goBack: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function
    }
  },
  data () {
    return {
      money: MONEY,
      loading: false
    }
  },
  computed: {
    disableBtn () {
      return !(this.value.vencimentoAte && this.value.pagamentoEm && this.value.peComissaoCompra)
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  beforeMount () {
    this.loading = true
    LojaBusiness.getPeComissaoCompra(this.value.lojaId)
      .then(response => {
        this.value.peComissaoCompra = response.data.toLocaleString('pt-BR', { minimumFractionDigits: 2 })
      })
      .catch(error => {
        this.$root.showErro(error.data)
      })
      .finally(() => {
        this.loading = false
      })
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loadingBtn = true
          this.nextStep()
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    }
  }
}
</script>
